import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import Resumecontent from "./ResumeContent";
import axios from "axios";
import pdf from "../../Assets/Soumyajit-Behera.pdf";
import { AiOutlineDownload } from "react-icons/ai";

function Resume() {
  const uri = "https://porfolio-backend.vercel.app/ranks/getRanks";
  const [spojRank, upadteSpojRank] = useState(0);
  const [hackerrank, upadteHackerank] = useState(0);
  const [sem, upadateSem] = useState(0);
  const [cgpa, upadteCgpa] = useState(0);

  useEffect(() => {
    axios
      .get(uri)
      .then((res) => {
        upadteSpojRank(res.data.message[0].spojRank);
        upadteHackerank(res.data.message[1].hackerrank);
        upadteCgpa(res.data.message[2].cgpa);
        upadateSem(res.data.message[3].sem);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Container fluid className="resume-section">
      <Particle />
      <Container>
        <Row className="resume">
          <Col md={6} className="resume-left">
            <h3 className="resume-title">Experience</h3>
            <Resumecontent
              title="CIRTIFICATE OF COMPLETION"
              date="2020 - 2024 "
              content={[
                "Completed one day Workshop o ARDUIO MICRO CONTROLLER BOARD and APPLICATION",
                "Aquired skill from Electronics and Communication at Toc H Institute "              ]}
            />
            <h3 className="resume-title">Extracurricular Activities</h3>
            <Resumecontent
              title="Web Developer"
              content={[
                "Worked on creating the frontend-end of the website using React js, Javascript.",
              ]}
            />
            <Resumecontent
              title="Football player"
              content={[
                "Team member of clubs DYFI panchayath road & LEO United ",
              ]}
            />
          </Col>
          <Col md={6} className="resume-right">
            <h3 className="resume-title">Education</h3>
            <Resumecontent
              title="Engineering "
              date="2018 - Present"
              content={[`CGPA: ${cgpa} (Till ${sem}th Sem)`]}
            />
            <Resumecontent
              title="12TH BOARD [St Mary's HSS ,Morakkala]"
              date="2018 - 2020"
              content={["Precentage: 79%"]}
            />
            <Resumecontent
              title="10TH BOARD [SMHSS, Pattimottom] "
              date="2017- 2018"
              content={["Precentage: 98%"]}
            />
          </Col>
        </Row>
      </Container>
    </Container>
  );
}

export default Resume;
